import React from 'react'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect
} from "react-router-dom";
import { Alumnos } from '../components/pages/Alumnos';
import { Citas } from '../components/pages/Citas';
import { Historial } from '../components/pages/Historial';
import { Index } from '../components/pages/Index';
import { Maestros } from '../components/pages/Maestros';
import { Admin } from '../components/pages/Admin';
import { CitaAgendada } from '../components/pages/CitaAgendada';
import { Estudiante } from '../components/pages/Estudiante';
import { Docente } from '../components/pages/Docente';
import { ModuloEstudiante } from '../components/pages/ModuloEstudiante';
import { ModuloDocente } from '../components/pages/ModuloDocente';

export const CitasRouter = () => {
    return (
        <Router>
            <Switch>
                <Route exact path="/" component={Index}></Route>
                <Route exact path="/alumnos" component={Alumnos}></Route>
                <Route exact path="/estudiante" component={Estudiante}></Route>
                <Route exact path="/modulo-estudiante" component={ModuloEstudiante}></Route>
                <Route exact path="/docente" component={Docente}></Route>
                <Route exact path="/modulo-docente" component={ModuloDocente}></Route>
                <Route exact path="/maestros" component={Maestros}></Route>
                <Route exact path="/cita" component={Citas}></Route>
                <Route exact path="/panel" component={Admin}></Route>
                <Route exact path="/historial" component={Historial}></Route>
                <Route exact path="/agradecimiento" component={CitaAgendada}></Route>
                <Redirect to="/" />
            </Switch>
        </Router>
    )
}

