import React, { useContext } from 'react'
import { useHistory } from 'react-router-dom'
import { Header } from './Header'
import { LoginContext } from '../../context/LoginContext';

export const CitaAgendada = () => {
    const history = useHistory();
    const { setLogin } = useContext(LoginContext);
    const finalizarCita = () => {
        setLogin([])
        history.replace('/');
    }
    return (
        <>
            <Header />
            <div className="container text-center mt-5">
                <h1 style={{ color: 'green' }}>La Dirección General de Educación Media Superior agradece su participación</h1>

                <button className="btn btn-danger btn-lg mt-5" onClick={finalizarCita}>Finalizar</button>
            </div>

        </>
    )
}
