import { useContext } from 'react'
import { Link, Redirect } from 'react-router-dom'
import { Header } from './Header'
import { LoginContext } from '../../context/LoginContext';

export const Admin = () => {
    const { login: data } = useContext(LoginContext);
    if (!data.familyName) {
        return (
            <Redirect to="/home" />
        );
    }
    return (
        <>
            <Header />
            <div className="container mt-5">
                <div className="row row-cols-1 row-cols-md-2 g-4">
                    <div className="col">
                        <Link style={{ textDecoration: 'none' }} to="/estudiante">
                            <div className="card">
                                <div className="card-body text-center">
                                    <img src="/student.png" alt="imgStudent" style={{ width: '200px' }} />
                                    <div className="texto_usuario" >Estudiantes</div>
                                </div>
                            </div>
                        </Link>
                    </div>
                    <Link style={{ textDecoration: 'none' }} to="/docente">
                        <div className="col">
                            <div className="card">
                                <div className="card-body text-center">
                                    <img src="/teacher.png" alt="imgTeacher" style={{ width: '200px' }} />
                                    <div className="texto_usuario">Docentes, directivos y personal administrativo</div>
                                </div>

                            </div>
                        </div>
                    </Link>
                </div>
            </div>

            <br />
            <br />

        </>
    )
}
