import { useContext } from 'react'
import { useHistory, Redirect } from 'react-router-dom';
import { Header } from './Header';
import { useForm } from '../../hooks/useForm';
import { LoginContext } from '../../context/LoginContext';

import { escuelas, tematicaDocente, poblacionDeImpactoDocente, figuraDocente } from '../../helpers/escuelas';

export const ModuloDocente = () => {
  const { login: data } = useContext(LoginContext);
  const bachilleratos = escuelas;
  const tematicas = tematicaDocente;
  const poblacionImpacto = poblacionDeImpactoDocente;
  const fDocente = figuraDocente;


  const [formState, handlerOnChange] = useForm({
    correo: data.email,
    figura: '',
    plantel: '',
    planDeEstudios: '',
    tematica: '',
    tema: '',
    poblacion: '',
    descripcion: '',
    requerimientos: ''
  })
  const { figura, plantel, planDeEstudios, tematica, tema, poblacion, descripcion, requerimientos } = formState;
  const history = useHistory();


  if (!data.familyName) {
    return (
      <Redirect to="/home" />
    );
  }
  const agregarEncuesta = async (ev) => {
    ev.preventDefault();
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(formState)
    }
    // const response = await fetch('https://citas-tutorias-dgems.herokuapp.com/api/alumnos', requestOptions);
    console.log(formState);
    // const response = await fetch('http://localhost:3500/api/maestros', requestOptions); //desarrollo
    const response = await fetch('https://buzon-dgems.herokuapp.com/api/maestros', requestOptions); //produccion
    console.log(response);
    await response.json();
    if (response.status === 200) {
      console.log("ingreso")
      history.replace('/agradecimiento');
    }
  }

  return (
    <>
      <Header />
      <div className="index__caja">
        <div className="row">
          <div className="col">
            <h1 className="index__titulo">Módulo para personal docente, asesores pedagógicos, coordinadores académicos, apoyo académico y directivo
            </h1>
          </div>
        </div>
      </div>
      <div className="container mt-5">

        <form onSubmit={agregarEncuesta}>

          <div className="mb-3 row">
            <label htmlFor="inputEscuela" className="col-sm-2 col-form-label">Desempeño laboral</label>
            <div className="col-sm-8">
              <select className="form-select" required aria-label="Default select example" id="inputFigura" name="figura" value={figura}
                onChange={handlerOnChange} >
                <option >Seleccione la figura que desempeña en el plantel.</option>
                {
                  fDocente.map((f) => (
                    <option key={f} value={f} >{f}</option>
                  ))
                }
              </select>
            </div>
          </div>

          <div className="mb-3 row">
            <label htmlFor="inputEscuela" className="col-sm-2 col-form-label">Plantel</label>
            <div className="col-sm-8">
              <select className="form-select" required aria-label="Default select example" id="inputEscuela" name="plantel" value={plantel}
                onChange={handlerOnChange} >
                <option >Selecciona el plantel en el que te encuentras inscrito.</option>
                {
                  bachilleratos.map((bachi) => (
                    <option key={bachi.escuela} value={bachi.escuela} >{bachi.escuela}</option>
                  ))
                }
              </select>
            </div>
          </div>
          <div className="mb-3 row">
            <label htmlFor="inputGrupo" className="col-sm-2 col-form-label">Plan de estudios</label>
            <div className="col-sm-8">
              <select className="form-select" required aria-label="Default select example" id="inputPlan" name="planDeEstudios" value={planDeEstudios} onChange={handlerOnChange} >
                <option >Selecciona el plan de estudios en el que te encuentras inscrito.</option>
                {
                  bachilleratos.map(resp => resp.escuela === plantel
                    && resp.plan.map(p => <option key={p}>{p}</option>))
                }
              </select>
            </div>
          </div>
          <div className="mb-3 row">
            <label htmlFor="inputEscuela" className="col-sm-2 col-form-label">Temática y propuesta</label>
            <div className="col-sm-8">
              <select className="form-select" required aria-label="Default select example" id="inputTipo" name="tematica" value={tematica}
                onChange={handlerOnChange} >
                <option >Selecciona el tipo de relación a tu propuesta.</option>
                {
                  tematicas.map((tipo) => (
                    <option key={tipo.tipo} value={tipo.tipo} >{tipo.tipo}</option>
                  ))
                }
              </select>
            </div>
          </div>
          <div className="mb-3 row">
            <label htmlFor="inputGrupo" className="col-sm-2 col-form-label">Tema</label>
            <div className="col-sm-8">
              <select className="form-select" required aria-label="Default select example" id="inputTema" name="tema" value={tema} onChange={handlerOnChange} >
                <option >Selecciona el tema referente al tipo de propuesta.</option>
                {
                  tematicas.map(resp => resp.tipo === tematica
                    && resp.tema.map(t => <option key={t}>{t}</option>))
                }
              </select>
            </div>
          </div>
          <div className="mb-3 row">
            <label htmlFor="inputEscuela" className="col-sm-2 col-form-label">Población de impacto</label>
            <div className="col-sm-8">
              <select className="form-select" required aria-label="Default select example" id="inputPoblacion" name="poblacion" value={poblacion}
                onChange={handlerOnChange} >
                <option >Población que impacta la propuesta y/o sugerencia.</option>
                {
                  poblacionImpacto.map((p) => (
                    <option key={p} value={p} >{p}</option>
                  ))
                }
              </select>
            </div>
          </div>
          <div className="mb-3 row">
            <label htmlFor="inputLigasMeet" className="col-sm-2 col-form-label">Descripción de la propuesta: </label>
            <div className="col-sm-8">
              <textarea required className="form-control" style={{ height: 150, resize: 'none' }} id="inputDescripcion" name="descripcion" value={descripcion} onChange={handlerOnChange}>
              </textarea>
            </div>
          </div>
          <div className="mb-3 row">
            <label htmlFor="inputLigasMeet" className="col-sm-2 col-form-label">Requerimiento para implementar la propuesta: </label>
            <div className="col-sm-8">
              <textarea required className="form-control" style={{ height: 150, resize: 'none' }} id="inputRequerimiento" name="requerimientos" value={requerimientos} onChange={handlerOnChange}>
              </textarea>
            </div>
          </div>
          <div className="d-flex justify-content-center mt-5 mb-5">
            <button type="submit" className="btn btn-success btn-lg">Enviar</button>
          </div>
        </form>

      </div>
    </>
  )
}
